.nav-title {
    font-size: 24px;
    font-weight: 700;
    font-family: Georgia, 'Times New Roman', Times, serif;
    display: inline-block;
    color: #f2f2f2;
    padding-left: 10px;
}

.nav {
  overflow: hidden;
  background-color: cornflowerblue;
}

.nav-link {
  float: left;
  color: #f2f2f2;
  background-color: cornflowerblue;
  text-align: center;
  padding: 15px 10px;
  text-decoration: none;
  font-size: 15px;

  /* these last two make the logout button in
  the nav bar look like another link */
  border:none;
  cursor: pointer;
  outline:0;
}

.nav-link:hover {
  background-color: darkslategray;
  border-radius: 8px;
}

.nav-right {
  float: right;
}

a:link {
  color: #f2f2f2;
  text-decoration: none;
  text-align: center;
}

a:visited {
  color: #f2f2f2;
  text-decoration: none;
}

/* #f2f2f2 */
footer {
   position: fixed;
   bottom: 0;
   width: 100%;
   height: 25px;   /* Height of the footer */
   background: cornflowerblue;
   text-align: center;
   color: black;
}

.span {
    margin: 5px 10px;
}


