body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0px;
}

.alert {
    background-color: #ff4545;
    margin: 50px;
}

form {
    background-image: -webkit-linear-gradient( rgb(185, 236, 252), rgb(255, 255, 255));
    background-image: linear-gradient( rgb(185, 236, 252), rgb(255, 255, 255));
    width: 30%;
    min-height: 250px;
    margin: 50px auto;
    padding: 25px;
    border-radius: 8px;
    box-shadow: 5px 5px 5px grey;
}

input {
    margin: 5px;
    margin-bottom: 10px;
}

.log-in, .log-out, .register {
  background-color: cornflowerblue;
  border-radius: 4px;
  color: #f8f8f8;
  min-height: 50px;
  min-width: 100px;
  outline:0;
  cursor: pointer;
}

.log-in:hover, .log-out:hover, .register:hover {
  background-color: #008183;
}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
    background:none;
    color:inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    border-bottom:1px solid #444; 
    cursor: pointer;
}

#welcome {
    margin: 20px;
    color: darkslategray;
}

.tagline {
    color: darkslategray;
    text-align: center;
}

.inputDiv {
    width: 100%;
    margin: auto;
    text-align: center;
}

img {
    padding:1px;
    border:1px solid #021a40;
 }

 #countDown {
    text-align: 'center';
    color: cornflowerblue;
    margin: 10;
 }

